* {
  padding: 0;
  margin: 0;
  
}

body {
  font-family: Roboto, sans-serif;
  background-color: #0c161e;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    max-width: 95%;
    min-width: 1254px;
    margin: auto;
    padding: 0 10px;
}

/* .App {
    max-width: 1280px;
    margin: auto;
    padding: 0 10px;
} */
.Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;
    margin-bottom: 20px;
    border-bottom: 4px solid #e2b100;
}

.Header__logo {
    color: #fff;
    text-transform: capitalize;
}

.Navigation {
    display: flex;
    flex-direction: row-reverse;
    list-style: none;
}

.Navigation__item {
    margin-left: 16px;
}

.Navigation__link {
    position: relative;
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;
    font-size: 20px;
}

.Navigation__link:after {
    position: absolute;
    top: 47px;
    left: 0;
    content: "";
    width: 0;
    height: 4px;
    background: #3f51b5;
    transition: width .3s linear;
}

.Navigation__link:hover:after {
    width: 100%;
}


.Navigation__link:hover {
    color: #ff5722;
}
.App__body {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}


.MovieCardCollection__msg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 38px;
  line-height: 1.5;
  text-transform: uppercase;
}
.MovieCard {
  position: relative;
  flex: 0 0 21%;
  margin-right: 2%;
  padding: 8px;
  margin-bottom: 16px;
  color: #424242;
  box-shadow: 0 0 2px 2px rgba(33, 33, 33, 0.5);
  background-color: #fff;
  border-radius: 2px;
}

.MovieCard__btn {
  position: absolute;
  outline: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

.MovieCard__btn:hover {
  box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
  background-color: #3f51b5;
}

.MovieCard__btn {
  position: absolute;
  display: block;
  outline: 0;
  border: 0;
  background-color: #e2b100;
  cursor: pointer;
  color: #0c161e;
  font-weight: 700;
  bottom: 8px;
  right: 8px;
  width: 35px;
  height: 35px;
  font-size: 24px;
  line-height: 35px;
  border-radius: 4px;
  box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
  transition: all 0.1s linear;
}
.SearchCategories__btn:hover {
  box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
  background-color: #3f51b5;
  color: #fff;
}

.MovieInfo__title {
  width: 90%;
  line-height: 1.5;
  margin-bottom: 10px;
  white-space: wrap;
}

.MovieInfo__descr {
  margin-bottom: 20px;
  font-size: 13px;
  line-height: 1.3;
}

.MovieInfo__rating {
  /* margin-bottom: 16px;
  font-size: 18px; */
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #e2b100;
  border: 1px solid #dea12f;
  width: 50px;
  height: 35px;
  font-weight: 700;
  line-height: 35px;
  text-align: center;
  border-bottom-right-radius: 35%;
}

.MovieCard__poster {
  display: block;
  width: 100%;
  /* height: 180px; */
  margin-bottom: 10px;
  object-fit: cover;
}

.MovieInfo__release {
  margin-bottom: 16px;
  font-size: 13px;
}

.MovieList {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex: 0 0 77%;
    height: 530px;
}


.App__sidebar {
  /* -ms-flex: 0 0 20%; */
  flex: 0 0 20%;
  padding-top: 20px;
}

.SearchForm {
  position: relative;
  /* display: -ms-flexbox; */
  display: flex;
  margin-bottom: 20px;
  padding: 8px;
  border-radius: 2px;
  background-color: #e2b100;
}

.SearchForm__input {
  padding: 8px;
  font-size: 18px;
  /* -ms-flex-positive: 1; */
  flex-grow: 1;
  color: #212121;
  border: 0;
  border-radius: 6px;
}

.SearchForm__btn {
  display: none;
  position: absolute;
  border: 0;
  outline: 0;
  padding: 4px 8px;
  text-transform: uppercase;
  background-color: #3f51b5;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
}

.SearchCategories {
  /* display: -ms-flexbox; */
  display: flex;
  /* -ms-flex-align: center; */
  align-items: center;
  /* -ms-flex-pack: distribute; */
  justify-content: space-around;
  margin-bottom: 30px;
}

.SearchCategories__btn {
  display: block;
  outline: 0;
  border: 0;
  background-color: #e2b100;
  cursor: pointer;
  padding: 10px 8px;
  text-transform: uppercase;
  white-space: nowrap;
  color: #0c161e;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  border-radius: 4px;
  /* -webkit-box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5); */
  box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
  /* -webkit-transition: all 0.1s linear; */
  /* -o-transition: all 0.1s linear; */
  transition: all 0.1s linear;
}

.Watchlist {
  position: relative;
  min-height: 350px;
  max-height: 72vh;
  overflow-y: auto;
  padding: 12px 8px;
  border-radius: 2px;
  background-color: #e2b100;
}

.Watchlist__title {
  /* display: -ms-flexbox; */
  display: flex;
  /* -ms-flex-pack: center; */
  justify-content: center;
  /* -ms-flex-align: center; */
  align-items: center;
  color: #0c161e;
  text-transform: uppercase;
  font-size: 28px;
  margin-bottom: 15px;
}

.Watchlist__msg {
  position: absolute;
  top: 50%;
  left: 50%;
  /* -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%); */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  font-size: 20px;
  color: #0c161e;
}





